<template>
  <div>
    <van-nav-bar left-arrow @click-left="$router.go(-1)" title="商品列表" />
    <div v-if="goodsList !== null">
      <van-card
        v-for="item in goodsList"
        :key="item.id"
        :price="item.price"
        :title="item.goodsname"
        :thumb="$preImg+item.img"
      >
        <template #footer>
          <van-button size="mini" icon="shopping-cart-o" type="primary" @click="add(item.id)"></van-button>
        </template>
      </van-card>
    </div>
    <div v-else>
      <van-empty
        class="custom-image"
        image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
        description="暂无数据"
      />
    </div>
  </div>
</template>

<script>
import { getGoodsList, addCart } from "../utils/req";
import { mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      goodsList: [],
    };
  },
  computed: {
    ...mapGetters({
      user: "user",
    }),
  },
  methods: {
    ...mapActions({
      reqCart: "cartListActions",
    }),
    reqGoodsList() {
      const fid = this.$route.query.fid;
      getGoodsList({ fid }).then((res) => {
        this.goodsList = res.data.list;
      });
    },
    add(goodsid) {
      var data = {
        uid: this.user.uid,
        num: 1,
        goodsid,
      };
      // 发起添加购物车请求
      addCart(data).then((res) => {
        // 1.提示消息添加成功
        this.$toast.success("添加购物车成功");
        // 2.更新购物车
        this.reqCart(this.user.uid);
      });
    },
  },
  mounted() {
    this.reqGoodsList();
  },
};
</script>

<style scoped>
</style>


